"use client"

import { useState, useEffect, useRef } from 'react'
import { MessageCircle, X, Send, Globe, Mountain, Sparkles } from 'lucide-react'
import { Button } from '@/components/ui/button'

interface Message {
  id: string
  text: string
  isUser: boolean
  timestamp: Date
  language?: string
}

interface ChatbotResponse {
  success: boolean
  response: string
  detectedLanguage: string
  supportedLanguages?: string[]
  error?: string
}

const LANGUAGE_OPTIONS = {
  'auto': 'üåê Auto-detect',
  'en': 'üá¨üáß English',
  'hi': 'üáÆüá≥ ‡§π‡§ø‡§Ç‡§¶‡•Ä',
  'bn': 'üáßüá© ‡¶¨‡¶æ‡¶Ç‡¶≤‡¶æ',
  'or': 'üáÆüá≥ ‡¨ì‡¨°‡¨º‡¨ø‡¨Ü',
  'ur': 'üáµüá∞ ÿßÿ±ÿØŸà'
};

const WELCOME_MESSAGES = {
  'en': '‡§®‡§Æ‡§∏‡•ç‡§§‡•á! Welcome to Jharkhand Tourism! How can I help you explore the beauty of Jharkhand today? üèîÔ∏è',
  'hi': '‡§®‡§Æ‡§∏‡•ç‡§§‡•á! ‡§ù‡§æ‡§∞‡§ñ‡§Ç‡§° ‡§™‡§∞‡•ç‡§Ø‡§ü‡§® ‡§Æ‡•á‡§Ç ‡§Ü‡§™‡§ï‡§æ ‡§∏‡•ç‡§µ‡§æ‡§ó‡§§ ‡§π‡•à! ‡§Æ‡•à‡§Ç ‡§Ü‡§ú ‡§ù‡§æ‡§∞‡§ñ‡§Ç‡§° ‡§ï‡•Ä ‡§∏‡•Å‡§Ç‡§¶‡§∞‡§§‡§æ ‡§ï‡§æ ‡§™‡§§‡§æ ‡§≤‡§ó‡§æ‡§®‡•á ‡§Æ‡•á‡§Ç ‡§Ü‡§™‡§ï‡•Ä ‡§ï‡•à‡§∏‡•á ‡§Æ‡§¶‡§¶ ‡§ï‡§∞ ‡§∏‡§ï‡§§‡§æ ‡§π‡•Ç‡§Ç? üèîÔ∏è',
  'bn': '‡¶®‡¶Æ‡¶∏‡ßç‡¶ï‡¶æ‡¶∞! ‡¶ù‡¶æ‡¶°‡¶º‡¶ñ‡¶£‡ßç‡¶° ‡¶™‡¶∞‡ßç‡¶Ø‡¶ü‡¶®‡ßá ‡¶∏‡ßç‡¶¨‡¶æ‡¶ó‡¶§‡¶Æ! ‡¶Ü‡¶ú ‡¶ù‡¶æ‡¶°‡¶º‡¶ñ‡¶£‡ßç‡¶°‡ßá‡¶∞ ‡¶∏‡ßå‡¶®‡ßç‡¶¶‡¶∞‡ßç‡¶Ø ‡¶Ö‡¶®‡ßç‡¶¨‡ßá‡¶∑‡¶£‡ßá ‡¶Ü‡¶Æ‡¶ø ‡¶ï‡ßÄ‡¶≠‡¶æ‡¶¨‡ßá ‡¶Ü‡¶™‡¶®‡¶æ‡¶ï‡ßá ‡¶∏‡¶æ‡¶π‡¶æ‡¶Ø‡ßç‡¶Ø ‡¶ï‡¶∞‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡¶ø? üèîÔ∏è',
  'or': '‡¨®‡¨Æ‡¨∏‡≠ç‡¨ï‡¨æ‡¨∞! ‡¨ù‡¨æ‡¨°‡¨ñ‡¨£‡≠ç‡¨° ‡¨™‡¨∞‡≠ç‡¨Ø‡≠ç‡≠ü‡¨ü‡¨®‡¨∞‡≠á ‡¨∏‡≠ç‡≠±‡¨æ‡¨ó‡¨§! ‡¨Ü‡¨ú‡¨ø ‡¨ù‡¨æ‡¨°‡¨ñ‡¨£‡≠ç‡¨°‡¨∞ ‡¨∏‡≠å‡¨®‡≠ç‡¨¶‡¨∞‡≠ç‡¨Ø‡≠ç‡≠ü ‡¨Ö‡¨®‡≠ç‡¨¨‡≠á‡¨∑‡¨£‡¨∞‡≠á ‡¨Æ‡≠Å‡¨Å ‡¨ï‡¨ø‡¨™‡¨∞‡¨ø ‡¨Ü‡¨™‡¨£‡¨ô‡≠ç‡¨ï‡≠Å ‡¨∏‡¨æ‡¨π‡¨æ‡¨Ø‡≠ç‡≠ü ‡¨ï‡¨∞‡¨ø‡¨™‡¨æ‡¨∞‡¨ø‡¨¨‡¨ø? üèîÔ∏è'
};

export default function ChatbotWidget() {
  const [isOpen, setIsOpen] = useState(false)
  const [selectedLanguage, setSelectedLanguage] = useState<string>('auto')
  const [showLanguageMenu, setShowLanguageMenu] = useState(false)
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: WELCOME_MESSAGES['en'],
      isUser: false,
      timestamp: new Date(),
      language: 'en'
    }
  ])
  const [inputMessage, setInputMessage] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isTyping, setIsTyping] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const languageMenuRef = useRef<HTMLDivElement>(null)

  // Auto-scroll to bottom when messages change
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  // Close language menu when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (languageMenuRef.current && !languageMenuRef.current.contains(event.target as Node)) {
        setShowLanguageMenu(false)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  // Change welcome message when language changes
  const changeLanguage = (langCode: string) => {
    setSelectedLanguage(langCode)
    setShowLanguageMenu(false)
    
    // Update welcome message if it's the first message
    if (messages.length === 1 && !messages[0].isUser) {
      const welcomeText = WELCOME_MESSAGES[langCode as keyof typeof WELCOME_MESSAGES] || WELCOME_MESSAGES['en']
      setMessages([{
        id: '1',
        text: welcomeText,
        isUser: false,
        timestamp: new Date(),
        language: langCode
      }])
    }
  }

  const sendMessage = async () => {
    if (!inputMessage.trim()) return

    const userMessage = inputMessage.trim();
    setInputMessage('');
    setMessages(prev => [...prev, { 
      id: Date.now().toString(),
      text: userMessage, 
      isUser: true,
      timestamp: new Date()
    }]);
    setIsLoading(true);
    setIsTyping(true);

    try {
      const response = await fetch('/api/chatbot/message', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: userMessage,
          language: selectedLanguage
        }),
      });

      const data: ChatbotResponse = await response.json();

      // Simulate typing delay for better UX
      setTimeout(() => {
        setIsTyping(false);
        
        if (data.success) {
          setMessages(prev => [...prev, { 
            id: Date.now().toString(),
            text: data.response, 
            isUser: false,
            timestamp: new Date(),
            language: data.detectedLanguage
          }]);
        } else {
          setMessages(prev => [...prev, { 
            id: Date.now().toString(),
            text: data.error || 'Sorry, I encountered an error. Please try again.', 
            isUser: false,
            timestamp: new Date()
          }]);
        }
      }, 1000);

    } catch (error) {
      console.error('Chatbot API error:', error);
      setIsTyping(false);
      setMessages(prev => [...prev, { 
        id: Date.now().toString(),
        text: 'Unable to connect to chatbot service. Please ensure the backend is running.', 
        isUser: false,
        timestamp: new Date()
      }]);
    } finally {
      setIsLoading(false);
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      sendMessage()
    }
  }

  return (
    <div 
      className="chatbot-widget-container"
      style={{
        position: 'fixed',
        bottom: '24px',
        right: '24px',
        zIndex: 9999,
        pointerEvents: 'auto'
      }}
    >
      {/* Floating Button */}
      {!isOpen && (
        <Button
          onClick={() => setIsOpen(true)}
          className="h-20 w-20 rounded-full shadow-2xl hover:shadow-3xl transition-all duration-300 transform hover:scale-110 group relative overflow-hidden"
          style={{
            background: 'linear-gradient(135deg, #800020 0%, #1e3a8a 100%)',
            border: '3px solid #f4d03f'
          }}
        >
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent -skew-x-12 transform -translate-x-full group-hover:translate-x-full transition-transform duration-1000"></div>
          <Mountain className="h-10 w-10 text-yellow-300 relative z-10" style={{ color: '#f4d03f' }} />
        </Button>
      )}

      {/* Chat Window */}
      {isOpen && (
        <div 
          className="shadow-2xl flex flex-col overflow-hidden backdrop-blur-sm"
          style={{ 
            position: 'fixed', 
            top: '60px', 
            right: '24px', 
            zIndex: 9999,
            width: '380px',
            maxWidth: '380px',
            height: '600px',
            minHeight: '600px',
            maxHeight: '600px',
            background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.08) 0%, rgba(30, 58, 138, 0.08) 100%)',
            backdropFilter: 'blur(20px)',
            WebkitBackdropFilter: 'blur(20px)',
            borderRadius: '25px',
            border: '3px solid #f4d03f'
          }}
        >
          {/* Header - Fixed */}
          <div 
            className="px-4 py-4 text-white flex items-center justify-between flex-shrink-0 relative overflow-hidden"
            style={{ 
              background: 'linear-gradient(135deg, #800020 0%, #1e3a8a 100%)',
              borderBottom: '2px solid #f4d03f'
            }}
          >
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/10 to-transparent animate-pulse"></div>
            <div className="flex items-center gap-3 relative z-10">
              <div className="relative">
                <Mountain className="h-5 w-5" style={{ color: '#f4d03f' }} />
                <div className="absolute -top-1 -right-1 w-3 h-3 bg-green-400 rounded-full animate-pulse border-2 border-white"></div>
              </div>
              <div>
                <span className="font-bold text-sm" style={{ color: '#f4d03f' }}>Jharkhand Tourism</span>
                <div className="text-xs flex items-center gap-1" style={{ color: '#f4d03f' }}>
                  <Sparkles className="h-3 w-3" style={{ color: '#f4d03f' }} />
                  AI Assistant
                </div>
              </div>
            </div>
            
            <div className="flex items-center gap-2 relative z-10">
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setIsOpen(false)}
                className="hover:bg-white/20 h-10 w-10 p-0 flex-shrink-0 rounded-full"
                style={{ backgroundColor: 'rgba(255, 192, 203, 0.2)' }}
              >
                <X className="h-6 w-6" style={{ color: '#ff69b4' }} />
              </Button>
            </div>
          </div>

          {/* Messages - Scrollable */}
          <div 
            className="flex-1 overflow-y-auto p-4 space-y-4 chatbot-scrollbar" 
            style={{
              background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.12) 0%, rgba(30, 58, 138, 0.12) 100%)',
              backdropFilter: 'blur(10px)',
              WebkitBackdropFilter: 'blur(10px)',
              height: '420px',
              maxHeight: '420px',
              overflowY: 'scroll'
            }}
          >
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex items-start gap-2 ${message.isUser ? 'flex-row-reverse justify-start' : 'justify-start'}`}
              >
                <div className="flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center text-lg" style={{ background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.2) 0%, rgba(30, 58, 138, 0.2) 100%)', border: '1px solid #f4d03f' }}>
                  {message.isUser ? 'üë§' : 'ü§ñ'}
                </div>
                <div
                  className={`max-w-[75%] px-4 py-3 text-sm break-words shadow-sm text-white`}
                  style={{ 
                    background: message.isUser 
                      ? 'linear-gradient(135deg, rgba(128, 0, 32, 0.15) 0%, rgba(30, 58, 138, 0.15) 100%)'
                      : 'linear-gradient(135deg, rgba(128, 0, 32, 0.12) 0%, rgba(30, 58, 138, 0.12) 100%)',
                    backdropFilter: 'blur(10px)',
                    WebkitBackdropFilter: 'blur(10px)',
                    border: '2px solid #f4d03f',
                    borderRadius: '20px',
                    wordWrap: 'break-word',
                    overflowWrap: 'break-word',
                    whiteSpace: 'pre-wrap',
                    color: 'white'
                  }}
                >
                  {message.text}
                </div>
              </div>
            ))}
            
            {(isLoading || isTyping) && (
              <div className="flex justify-start items-start gap-2">
                <div className="flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center text-lg" style={{ background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.2) 0%, rgba(30, 58, 138, 0.2) 100%)', border: '1px solid #f4d03f' }}>
                  ü§ñ
                </div>
                <div 
                  className="px-4 py-3 shadow-md" 
                  style={{
                    background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.12) 0%, rgba(30, 58, 138, 0.12) 100%)',
                    backdropFilter: 'blur(10px)',
                    WebkitBackdropFilter: 'blur(10px)',
                    border: '2px solid #f4d03f',
                    borderRadius: '20px'
                  }}
                >
                  <div className="flex space-x-1">
                    <div className="w-2 h-2 bg-white rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-yellow-300 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-white rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            )}
            
            {/* Invisible div to scroll to */}
            <div ref={messagesEndRef} />
          </div>

          {/* Input - Fixed */}
          <div 
            className="p-4 border-t-2 border-yellow-200 flex-shrink-0"
            style={{
              background: 'linear-gradient(135deg, rgba(128, 0, 32, 0.1) 0%, rgba(30, 58, 138, 0.1) 100%)',
              backdropFilter: 'blur(15px)',
              WebkitBackdropFilter: 'blur(15px)'
            }}
          >
            <div className="flex items-center gap-3">
              <input
                type="text"
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="Ask about Jharkhand tourism..."
                className="flex-1 px-4 py-3 border-2 border-gray-200 rounded-full text-sm focus:outline-none focus:ring-2 focus:ring-yellow-400 focus:border-yellow-400 transition-all duration-200"
                style={{ 
                  background: 'rgba(255,255,255,0.1)',
                  backdropFilter: 'blur(10px)',
                  WebkitBackdropFilter: 'blur(10px)',
                  color: 'white',
                  height: '40px'
                }}
              />
              <Button
                onClick={sendMessage}
                disabled={!inputMessage.trim() || isLoading}
                size="sm"
                className="h-10 w-10 p-0 rounded-full flex-shrink-0 shadow-lg hover:shadow-xl transition-all duration-200"
                style={{ 
                  background: 'linear-gradient(135deg, #800020 0%, #1e3a8a 100%)',
                  border: '2px solid #f4d03f',
                  opacity: (!inputMessage.trim() || isLoading) ? 0.5 : 1
                }}
              >
                <Send className="h-4 w-4" style={{ color: '#f4d03f' }} />
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
